body,
html {
	margin: 0;
	padding: 0;
	background-color: #000000;
	animation-name: fadeInOpacity;
	animation-iteration-count: 1;
	animation-timing-function: ease-in;
	animation-duration: 0.6s;
	font-family: MessinaSansWeb, Helvetica, Arial !important;
}

@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

.applicationWrapper {
	background-color: #a84e43;
	display: grid;
	grid-template-columns: repeat(20, 1fr);
	grid-template-rows: repeat(20, 1fr);
	width: 100%;
	height: 100vh;
}

.backgroundImg {
	grid-column: 1/21;
	grid-row: 1/21;
	height: 100%;
	width: 100%;

}

.discordWrapper {
	grid-column: 14/18;
	grid-row: 1/21;
	align-self: center;
	justify-self: center;
	z-index: 200;
}

.iFrameDiscord {
	height: 600px;
	width: 350px;
}

.descriptionWrapper {
	grid-column: 3/10;
	grid-row: 1/19;
	align-self: center;
	justify-self: center;
	z-index: 200 !important;
}

p {
	color: #74da72;
	font-size: 45px;
	font-weight: 800;
}

.subParagraph {
	color: #fff;
	font-size: 25px;
	font-weight: 300;
}

@media only screen and (max-width: 1050px) {
	.descriptionWrapper {
		grid-column: 1/21;
		grid-row: 1/1;
		align-self: center;
		justify-self: center;
		z-index: 200 !important;
	}

	p {
		text-align: center;
	}

	span {
		text-align: center;
	}

	.discordWrapper {
		grid-column: 1/21;
		grid-row: 2/2;
		align-self: center;
		justify-self: center;
		z-index: 200;
	}
}